body {
  margin: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  flex-wrap: wrap;
  font-size: 100px;
  font-family: Arial, Helvetica, sans-serif;
}

.block {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;

  height: 300px;
  flex-basis: 33%;
  min-width: 300px;
  color: white;
}

.block--1 {
  background-color: #f00;
}

.block--2 {
  background-color: #c00;
}

.block--3 {
  background-color: #900;
}

.block--4 {
  background-color: #600;
}

.block--5 {
  background-color: #300;
}

.block--6 {
  background-color: #000;
}

@media (min-width: 600px) and (max-width: 899px) {
  .block--3 {
    order: 1;
  }

  .block--5,
  .block--6 {
    order: 3;
  }
}

@media (min-width: 900px) {
  .block--6 {
    order: 1;
  }

  .block--5 {
    order: 2;
  }

  .block--4 {
    order: 3;
  }
}
